import { Injectable } from '@angular/core';
import {
  IRecordGridColumnNamedFilterOption,
  IRecordGridNamedFilterOptionsMap
} from '../../../common/types/record-grid.types';
import { IRecordGridDesignInspectorColumnConfig } from '../record-grid-column-editor-control/record-grid-column-editor.types';
import { toNumber } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class RxRecordGridFilterSelectHelperService {
  getNamedFilterOptionsFromColumns(
    columns: IRecordGridDesignInspectorColumnConfig[]
  ): IRecordGridNamedFilterOptionsMap {
    return columns.reduce((result: IRecordGridNamedFilterOptionsMap, col) => {
      if (col.namedFilterOptions?.length) {
        result[col.fieldId] = col.namedFilterOptions
          .map(
            (column) =>
              ({
                guid: column.guid,
                ...column.data,
                index: toNumber(column.data.index)
              } as IRecordGridColumnNamedFilterOption)
          )
          .sort((a, b) => a.index - b.index);
      }

      return result;
    }, {});
  }
}
